
import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { Connect } from '../../providers/connect';
import { SponsorPage } from '../sponsor/sponsor';

@Component({
  selector: 'page-tipping',
  templateUrl: 'tipping.html',
})
export class TippingPage {

 public fix : any[];
 public round : any = 0;
 public name : any = '';
 public ladder : any = '';
 public standings : any = '';
 public winners : any = '';
 public mode : any = 0;
 public banner : any = './assets/img/blankbanner.jpg';

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public connect: Connect, public alertCtrl : AlertController) {
     this.fix = [];
     this.round=0;
  }

  ionViewDidLoad() {
        let that=this;
        let url = 'zs_tipfixtures.php?id='+window.localStorage.getItem('tiplayer');
        this.connect.getList(url).subscribe(
         data => {
            that.fix = data.fix;
            that.round = data.round;
            that.name = data.name;
            that.ladder = data.ladder;
            that.winners = data.winners;
            that.standings = data.standings;
            that.banner = data.banner;
         },
         err => {
         }
        ); 
  }

  choose(a,b) {
    switch (b) {
      case 1 : this.fix[a].choice = this.fix[a].name1; break;
      case 2 : this.fix[a].choice = this.fix[a].name2; break;
      default: this.fix[a].choice = 'DRAW';
    }
    this.fix[a].pick = b; 
  }

  gomode(a) {
    this.mode = a;
  }

  dosubmit()
  { 

       let ss='';
       for(let i=0; i < this.fix.length; i++)
         ss=ss+'^'+this.fix[i].pick;
       let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'zs_sendtips.php?id='+window.localStorage.getItem('tiplayer')+'&round='+that.round+'&tips='+ss;
console.log(url);

       this.connect.getList(url).subscribe(
          data => {
            loading.dismiss();
            if (data.success) 
               that.doOk();
            else
               that.doError();
          },
          err => {
            loading.dismiss();
          }
        );

    
  }

  doOk() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Good Luck!',
      subTitle: 'Your tips have been entered',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  doError() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Warning',
      subTitle: 'Tips could not be accepted at this time',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }


 showSponsor() {
    this.navCtrl.push(SponsorPage, { id : 2});
 }
}




