import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

import { Camera, CameraOptions } from '@ionic-native/camera';
import { HTTP } from '@ionic-native/http';


@Component({
  selector: 'page-newprof',
  templateUrl: 'newprof.html',
})
export class NewprofPage {

  public pl : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public connect: Connect, private camera: Camera, public http : HTTP, public loadingCtrl: LoadingController) {
    this.pl = {
      me : 0,
      id : 0,
      first : '',
      middle : '',
      last : '',
      dob : '',
      favplayer : '',
      position : '',
      favteam : '',
      bio : '',
      awards : '',
      img : 'https://theparcelpeople.com.au/soccer/general.jpg',
      picdata : ''
    }
      this.pl.me = window.localStorage.getItem('myac');
  }

  ionViewDidLoad() {
      this.pl.me = window.localStorage.getItem('myac');
  }

  getpicture() {
 
   const options: CameraOptions = {
     quality: 70,
     targetWidth:500,
     targetHeight:500,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pl.picdata = imageData;
    that.pl.img = 'data:image/jpeg;base64,' + imageData;
 }, (err) => {
//    alert(JSON.stringify(err));
 });

 }

  getcameraroll() {
   const options: CameraOptions = {
     quality: 70,
     targetWidth:500,
     targetHeight:500,
     sourceType: 0,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pl.picdata = imageData;
    that.pl.img = 'data:image/jpeg;base64,' + imageData;
 }, (err) => {
//    alert(JSON.stringify(err));
 });

 }

 doEdit() {
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://pikcup.com.au/test/zs_newprofile.php';
    this.http.post(url, that.pl, {})
      .then(data => {
            loading.dismiss();
         window.localStorage.setItem('newplayer', data.data);
 	  that.navCtrl.pop();
    })
    .catch(error => {
            loading.dismiss();
     
  });

 }


}



