import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-past',
  templateUrl: 'past.html',
})
export class PastPage {

 public feed : any[];
 public myAc : any = '';

  constructor(public navCtrl: NavController, private connect : Connect, public alertCtrl: AlertController) {
    this.feed = [];
  }

  ionViewDidEnter()  {
    this.recalc();
  }

  recalc() {
     this.myAc = window.localStorage.getItem('myac');
     if (!this.myAc) this.myAc='';
     let that=this;
        let url = 'zs_socialfeed.php?mine=1&me='+that.myAc;
        this.connect.getList(url).subscribe(
         data => {
            that.feed = data.feed;
         },
         err => {
         }
        ); 
  }


  takedown(a) {
      let that=this;
      let tid=this.feed[a].id;
      let alert = this.alertCtrl.create({
      title: 'Remove Post - Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
           let url = 'zs_removepost.php?id='+tid;
           this.connect.getList(url).subscribe(
            data => {
              that.navCtrl.pop();
            },
            err => {
            }
           ); 

          }
        }
      ]
    });
    alert.present();
 }

 

}




