import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  name : any = '';
  img : any = 'https://theparcelpeople.com.au/catalogue/server/pback.png';
  bio : any = '';
  awards : any = '';
  position: any = '';
  favplayer : any = '';
  favteam : any = '';
  dob : any = '';
  id : any = 1;
  firstname : any = '';
  lastname : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
     let i = this.navParams.get('player');
     this.id = i.id;
  }

  ionViewDidLoad() {
    this.img = 'https://theparcelpeople.com.au/catalogue/server/zs_playercard.php?id='+this.id;
  }

}



