import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { TippingPage } from '../tipping/tipping';
import { NewprofPage } from '../newprof/newprof';

@Component({
  selector: 'page-tiphome',
  templateUrl: 'tiphome.html',
})

export class TiphomePage {

  public players : any[];
  public myAc : any = '';
  public blurb : any = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    this.players = [];
  }

  ionViewDidEnter() {
     this.myAc = window.localStorage.getItem('myac');
     if (!this.myAc) this.myAc='';
     let that=this;
     let n = window.localStorage.getItem('newplayer');
     if (!n) n='0';
     window.localStorage.removeItem('newplayer');
     if (parseInt(n)>0) {
       window.localStorage.setItem('tiplayer', n);
       this.navCtrl.push(TippingPage);
     }
     else {   
       let url = 'zs_myplayers.php?id='+this.myAc;
       this.connect.getList(url).subscribe(
       data => {
           that.players = data.players;
           that.blurb = data.blurb;
       },
       err => {
         }
       ); 
     }
  }

  newreg() {
    window.localStorage.removeItem('goback');
    window.localStorage.removeItem('newplayer');
    this.navCtrl.push(NewprofPage);
  }

 goPlayer(a) {
   window.localStorage.setItem('tiplayer', a);
   this.navCtrl.push(TippingPage);
 }

}



