import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-history',
  templateUrl: 'history.html',
})
export class HistoryPage {

 public hist : any[];
  constructor(public navCtrl: NavController, private connect : Connect, public alertCtrl: AlertController) {
    this.hist = [];
  }

  ionViewDidEnter()  {
     let m = window.localStorage.getItem('myac');
     let that=this;
        let url = 'zs_history.php?me='+m;
        this.connect.getList(url).subscribe(
         data => {
            that.hist = data.hist;
         },
         err => {
         }
        ); 

  }


}




