import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ProfilePage } from '../profile/profile';

/**
 * Generated class for the TeamplayersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-teamplayers',
  templateUrl: 'teamplayers.html',
})
export class TeamplayersPage {
  players : any = [];
  feed : any = [];
  teamname : any;
  team : any = 0;
  mode : any  = 3;
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    let tt = this.navParams.get('t');
    this.teamname = tt.name;
    this.team = tt.id;
  }

  ionViewDidLoad() {
     this.mode = 3;
     let that=this;
        let url = 'zs_teaminfo.php?id='+this.team;
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
            that.feed = data.feed;
         },
         err => {
         }
        ); 

  }
 
  gomode(a) {
    this.mode = a;
  }

  goPlayer(p) {
    this.navCtrl.push(ProfilePage,{player : p});
  }

uptick(a)
 { if (!this.feed[a].liked) {

     this.feed[a].likes++;
     this.feed[a].liked=true;
     let that=this;
        let url = 'zs_uptick.php?id='+this.feed[a].id;
        this.connect.getList(url).subscribe(
         data => {
         },
         err => {
         }
        ); 
   }
 }

}



