import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { PlayersPage } from '../players/players';
import { LoginPage } from '../login/login';
import { MembershipPage } from '../membership/membership';
import { TeamsPage } from '../teams/teams';
import { SponsorPage } from '../sponsor/sponsor';
import { SocialuploadPage } from '../socialupload/socialupload';
import { InfoPage } from '../info/info';
import { Connect } from '../../providers/connect';
import { MarketPage } from '../market/market';
import { MyprofilesPage } from '../myprofiles/myprofiles';
import { TiphomePage } from '../tiphome/tiphome';
import { HistoryPage } from '../history/history';
import { EventsPage } from '../events/events';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

 public thisversion : any = 1;
 public feed : any[];
 public banner : any = './assets/img/blankbanner.jpg';
 public myAc : any = '';
 public hmode : any = 0;
 public manager : any = false;
  constructor(public navCtrl: NavController, private connect : Connect, public alertCtrl: AlertController) {
    this.feed = [];
    this.manager = false;
    this.hmode=0;
    this.thisversion = 1; 
  }

  ionViewDidEnter()  {
     this.myAc = window.localStorage.getItem('myac');
     if (!this.myAc) this.myAc='';
     window.localStorage.removeItem('newplayer');
     let that=this;
        let url = 'zs_socialfeed.php?me='+that.myAc;
        this.connect.getList(url).subscribe(
         data => {
            that.feed = data.feed;
            that.banner = data.banner;      
            that.manager = data.manager;    
            let v = parseInt(data.version);
            if (v > that.thisversion)
               that.versionmessage(v);
         },
         err => {
         }
        ); 

     if (this.myAc.length) {
       let m = this.hmode;
       this.hmode=0;
       if (m==1) this.navCtrl.push(MembershipPage);
       if (m==2) this.navCtrl.push(MyprofilesPage);
       if (m==3) this.navCtrl.push(TiphomePage);
       if (m==4) this.navCtrl.push(HistoryPage);
     }
  }

 goEvents() {
  this.navCtrl.push(EventsPage);

 }

 goMarket() {
  this.navCtrl.push(MarketPage, {id: 1});
 }


 goInfo(a) {
   this.navCtrl.push(InfoPage, { id : a });
 }

  teams()
 { this.navCtrl.push(TeamsPage);
 }

 showSponsor() {
    this.navCtrl.push(SponsorPage, { id : 1});
 }

  showPlayers() {
    this.navCtrl.push(PlayersPage);
  }

  tipping() {
    if (this.myAc.length) this.navCtrl.push(TiphomePage); 
    else {
      this.hmode=3;
      this.navCtrl.push(LoginPage);
    }
  }


  showUpload() {
    this.navCtrl.push(SocialuploadPage);
  }

  doLogout()  {
    this.myAc='';
    window.localStorage.removeItem('myac');
    window.localStorage.removeItem('userPayment');

  } 

  login()  {
    this.navCtrl.push(LoginPage);
  } 

  membership() {
    if (this.myAc.length) this.navCtrl.push(MembershipPage); 
    else {
      this.hmode=1;
      this.navCtrl.push(LoginPage);
    }
  }


  history() {
    if (this.myAc.length) this.navCtrl.push(HistoryPage); 
    else {
      this.hmode=4;
      this.navCtrl.push(LoginPage);
    }
  }

  myprofiles() {
    if (this.myAc.length) this.navCtrl.push(MyprofilesPage); 
    else {
      this.hmode=2;
      this.navCtrl.push(LoginPage);
    }
  }

  signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
            that.doLogout();
          }
        }
      ]
    });
    alert.present();
 }


  versionmessage(a) {
      this.thisversion = a;
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'New Version Available',
      subTitle: 'Please go to the App Store or Google Play Store to get the latest version of this App',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
          }
        }
        
      ]
    });
    alert.present();
 }


 uptick(a)
 { if (!this.feed[a].liked) {

     this.feed[a].likes++;
     this.feed[a].liked=true;
     let that=this;
        let url = 'zs_uptick.php?id='+this.feed[a].id;
        this.connect.getList(url).subscribe(
         data => {
         },
         err => {
         }
        ); 
   }
 }

}




