import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';

import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-payment',
  templateUrl: 'payment.html',
})
export class PaymentPage {

  public payment: any = '';
  public paycard : any = '';
  public pmethods : any[];
  public options : any[];
  public allok : any = false; 
  public pmode : any = 1;
  public id : any = 0;
  public tick : any = './assets/img/tick.png';

  public blank : any = './assets/img/blank.png';

  public stripe_pub: any = '';
  public teamname : any = '';
  public name : any = '';
  public mymethod : any = 1;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public connect: Connect, public alertCtrl : AlertController) {
       this.pmethods = [];
       this.options = [];
       this.allok = false;
       this.pmode = 1;
       this.mymethod = 1;
       this.id = this.navParams.get('id');
  }

  ionViewDidEnter() {
    this.recalc();
  }

  chooseOption(a) {
    this.mymethod = a;
    for (var i=0; i< this.options.length; i++) {
      if (this.options[i].id == a) this.options[i].selected = 1;
      else this.options[i].selected=0;
    }
  }

    recalc() {
     this.payment = window.localStorage.getItem('userPayment');
     if (!this.payment)
       this.payment='';
     if (this.payment.length) 
        this.allok = true;

         let that=this;
	    let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();
         let url='zs_getpaymentdetails.php?pmode='+this.pmode+'&id='+this.id+'&payment='+this.payment;
      console.log(url);
         this.connect.getList(url).subscribe(
         data => {
             loader.dismiss();
             that.paycard = data.paycard;
             that.pmethods = data.pmethods;
             that.stripe_pub = data.stripe_pub;
             that.payment = data.payment;
             that.teamname = data.teamname; 
             that.name = data.name;
             that.options = data.options;

             window.localStorage.setItem('userPayment',that.payment);
	     if (that.payment.length) 
        	that.allok = true;
             else
                that.allok=false;
            that.chooseOption(that.mymethod);
         },
         err => { loader.dismiss();
                this.connect.logError(err);
         }
        ); 

  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Registration is complete',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             window.localStorage.setItem('goback','yes');
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  doError() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Warning',
      subTitle: 'Payment could not be completed - Please try again',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }


  showPayment(a)
  { window.localStorage.setItem('userPayment',a);
    this.pmode=0;
    this.recalc();
  }
  
  showEnterPayment() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }

  doConfirm() {
            let that = this;
	    let loader = this.loadingCtrl.create({
	      content: "Please wait..."
	    });
	    loader.present();
            let url='zs_savepayment.php?reg='+this.id+'^'+this.payment+'^'+this.mymethod;
          //  this.myAlert(url);
             this.connect.getList(url).subscribe(
	         data => {
		    loader.dismiss();
                    if (data.success) 
                      that.doComplete();
                    
                    else
                      that.doError();
                 },
	         err => {
                    loader.dismiss();
                    this.connect.logError(err);
                 }
	        );


  }

}



