import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-events',
  templateUrl: 'events.html',
})
export class EventsPage {

  public button : any = '';
  public amount : any = 0.00;
  public eventimage = './assets/img/blank.jpg';  
 
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
  }

  ionViewDidLoad() {
        let that=this;
        let url = 'zs_event.php';
        this.connect.getList(url).subscribe(
         data => {
            that.eventimage = data.eventimage;
            that.amount = data.amount;
            that.button = data.button;
         },
         err => {
         }
        ); 
  }

}



